@import "src/styles/colors.module";

.ContactsPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 90%;

  &Header {
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 44px;
    color: $dark-font-color;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &Mail {
    padding-left: 10px;
    font-style: normal;
    font-weight: 300;
    font-size: 36px;
    line-height: 44px;
    color: $dark-font-color;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
  }
}
