@import "src/styles/colors.module";

.SkillsList {
  list-style-type: disc;

  ::marker {
    color: $light-gray-color;
    font-size: 0.5rem;
  }

  &Item {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 42px;

    &Hoverable {
      &:hover {
        background-color: #FAFAFA;
      }
    }
  }

  &WithLevel {
    display: grid;
    grid-template-columns: 1fr auto;
  }
}
