@import "src/styles/colors.module";

.SkillsLevel {
  display: flex;
  gap: 2px;
  align-items: center;
  justify-content: flex-end;
  padding-left: 10px;
  padding-right: 10px;
  background-color: rgba(210, 220, 210, 0.1);
  border-radius: 8px;
  margin: 4px;

  &Marker {
    background-color: $blue-color;
    border-radius: 4px;
    width: 10px;
    height: 10px;

    &Empty {
      background-color: $light-gray-color;
      opacity: 0.3;
    }
  }
}
